import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Carousel, Image } from 'react-bootstrap';
import Loader from './Loader';
import Message from './Message';
import { listFeaturedProducts } from '../actions/productActions';

const ProductCarousel = () => {
  const dispatch = useDispatch();

  const featuredProduct = useSelector((state) => state.featuredProduct);
  const { loading, error, products } = featuredProduct;

  useEffect(() => {
    dispatch(listFeaturedProducts());
  }, [dispatch]);

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant='danger'>{error}</Message>
  ) : (
    <Carousel pause='hover' className='bg-dark'>
      {products.map((product) => (
        <Carousel.Item key={product._id}>
          <Link to={`/product/${product._id}`}>
            <Image
              src={product.image}
              alt={product.name}
              className='carousel-image'
            />
            <Carousel.Caption className='carousel-caption'>
              <h2 className='carousel-caption--h2'>{product.name}</h2>
            </Carousel.Caption>
          </Link>
        </Carousel.Item>
      ))}
    </Carousel>
  );
};

export default ProductCarousel;
